.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

ul.todos li {
  border: 2px solid #f2f2f2;
  padding: 5px;
  margin-bottom: 5px;
  text-align: left;
  overflow: hidden;
  line-height: 30px;
}

ul.todos li button {
  float: right;
  display: inline-block;
  margin: 0 2px;
}

ul.todos li button.fixed-width {
  width: 100px;
}

.completed {
  text-decoration: line-through;
}
